.run-code-btn {
  position: absolute;
  top: 0.5rem;
  width: 1.5em;
  height: 1.5em;
  cursor: pointer;
  color: var(--md-default-fg-color--lightest);
  transition: color .25s;
  z-index: 1;
  /*outline: 1px dashed red;*/
}

.run-code-btn:focus, .run-code-btn:hover, :hover>.run-code-btn {
  color: var(--md-accent-fg-color);
}

.run-code-btn::after {
  display: block;
  background-color: currentcolor;
  height: 1rem;
  margin: 0 auto;
  content: "";
}

.run-code-btn.play-btn {
  padding-left: 2px;
  right: 3em;
}

.run-code-btn.play-btn::after {
  width: 0.9em;
  -webkit-mask-image: url('data:image/svg+xml;charset=utf-8;base64,');
}


.run-code-btn.reset-btn {
  right: 5em;
}

.run-code-btn.reset-btn::after {
  width: 1.2em;
  -webkit-mask-image: url('data:image/svg+xml;charset=utf-8;base64,');
}

.run-code-title {
  margin-top: 0 !important;
  border-top: 0.05rem solid var(--md-default-fg-color--lightest)
}

.hide-code {
  margin: 0 !important;
  height: 0 !important;
  padding: 0 !important;
}

.cm-editor {
  background-color: var(--md-code-bg-color);
}

.run-code-hidden {
  display: none;
}
